.list-group-item{
  background-color: #f3f3f3;
}

.list-group-item.large{
  border: #555555 solid 5px;
}

.list-group-item.medium{
  border: #555555 solid 3px;
  padding: 10px 10px;
}

.list-group-item.small{
  border: #555555 solid 1px;
  padding: 10px 10px;
}

.rank.large{
  font-size: 30px;
  font-weight: bold;
  line-height:30px;
}

.rank.medium{
  display:inline-block;
  font-size: 20px;
  font-weight: bold;
  margin-right: 12px;
  margin-bottom: 0px;
  width: 20px;
}

.rank.small{
  display:inline-block;
  font-size: 20px;
  font-weight: bold;
  margin-right: 12px;
  margin-bottom: 0px;
  width: 20px;
}

.list-image.large {
  display: inline-block;
  float: right;
  width: 20%;
  height: auto;
}

.list-image.medium {
  display: inline-block;
  float: right;
  width: 10%;
  height: auto;
}

.list-image.small {
  display:none;
}

.link.large {
  /* color: #1db954; */
  line-height: 52px;
  display: inline-block;
  max-width: 80%;
  font-size: 50px;
  color: #17a2b8;
}

.link.medium{
  line-height: 32px;
  display: inline-block;
  max-width: 87%;
  font-size: 30px;
  color: #17a2b8;
}

.link.small{
  line-height: 30px;
  display: inline-block;
  max-width: 95%;
  font-size: 30px;
  color: #17a2b8;
}

.link:hover {
  /* color: #1ed760; */
  color: #0f6f89;
  text-decoration: none;
}


.info.large {
  display: inline-block;
  color: #999999;
  font-size: 22px;
  padding-left: 5px;
  max-width: 80%;
}

.info.medium {
  display: inline-block;
  color: #999999;
  font-size: 16px;
  /* padding-left: 35px; */
  padding-left: 32px;
  max-width: 87%;
}

.info.small {
  display: inline-block;
  color: #999999;
  font-size: 16px;
  line-height: 16px;
  padding-left: 32px;
  max-width: 87%;

}

.info:hover {
  color: #888888;
  text-decoration: none;
}

.genres{
  margin-bottom: 0px;
}

.genres:hover {
  color: #999999 !important;
}
